import React from 'react';
import '../css/about/about.css';
import Cards from '../components/Cards';
import TitleComponent from '../components/TitlePage';

const About = () => {

    const calculate_age = dob => {
        const birthDate = new Date(dob); 
        const difference = Date.now() - birthDate.getTime();
        const age = new Date(difference);    
        return Math.abs(age.getUTCFullYear() -1970 );
    }

    const cache = {};
    function importAll(r) {r.keys().forEach((key) => (cache[key] = r(key)));}
    importAll(require.context("../photos/logo", false, /\.(png|jpe?g|svg)$/));
    const images = {image:{},text:[],}

    function getImages(){Object.entries(cache).forEach((module,index) =>(images.image[index]= module[1].default,images.text[index] = module[0].replace("./","")));}
    getImages()

    const img = imgtext => {return images.image[images.text.indexOf(imgtext)];}

    
    return(
        <div className="page">
            <TitleComponent title="Despre"/>
               <div className="about">
                   <h1>Cine sunt ?</h1>

                   <p className="text">&emsp;Numele meu este Abrudan Titus-Daniel, am {calculate_age("12-4-1999")} ani si imi place sa invat limbaje noi de programare, algoritmica, moduri noi de organizare si dezvoltare a programelor astfel incat sa functioneze cat mai bine intr-un timp foarte scurt.</p>

                   <h1>Ce folosesc pentru programare ?</h1>

                   <div className="cardplace">              
                   <Cards height="450px"  image={img("c.png")} title="C-Sharp" text="&emsp;In cele mai multe dintre cazuri am folosit acest limbaj mai ales cand vine vorba despre web sau executabile. Putem utiliza OOP si are o sintaxa ordonata, se aseamana foarte mult cu JAVA." />
                   <Cards height="450px"  image={img("netcore.png")} title="Net Core" text="&emsp;Net Core este un framework de dezvoltare a aplicatiilor web, l-am folosit alaturi de limbajul C# si framework-ul relational Entity Framework pentru a implementa mai usor backend-ul aplicatiilor." />
                   <Cards height="450px" image={img("react.png")} title="React" text="&emsp;Dezvoltat de catre cei de la facebook, React este o librarie JavaScript cu ajutorul careia am implementat frontend-ul diferitor aplicatii. Din punct de vedere a sintaxei imi pare greu de utilizat, dar merita in urma rezultatelor. " />
                   <Cards height="450px" image={img("arduino.png")} title="Arduino" text="&emsp;Arduino este o platforma pentru dezvoltarea produselor electronice. Pentru programarea acestora am utilizat limbajul C/C++, iar posibilitatea de a insera biblioteci predefinite usureaza munca." />
                   <Cards height="450px" image={img("postgresql.png")} title="PostgreSQL" text="&emsp;Mai bine de 30 de ani, PostgreSQL este o baza de date relationala. Imi place sa o folosesc deoarece ofera un bun mod de organizare. " />
                   <Cards height="450px" image={img("mysql.png")} title="MySQL" text="&emsp;MySQL, o baza de date relationala, usor de modelat si folosita de multe companii. Am utilizat aceasta baza alaturi de limbajul PHP pentru a dezvolta mici proiecte." />         
                   </div>

                   <h1>Ce vreau sa invat ?</h1>

                   <div className="cardplace">
                    <Cards height="250px" image={img("nodejs.png")} title="NodeJS" link="" />
                    <Cards height="250px" image={img("java.png")} title="Java" link="" />
                    <Cards height="250px" image={img("mongodb.png")} title="MongoDB" link="" />
                    <Cards height="250px" image={img("raspberrypi.png")} title="Raspberry Pi" link="" />
                   </div>

               </div>
        </div>
    )
}

export default About